.ContainerMenuPrestador{
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: var(--branco);
    
    & .FlatListMenuPrestador{ 
        flex: 1;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        padding: 20px;
        height: 100%;
        overflow-y: auto;
    }
}
