.ContainerMenuAdministracao{
    min-height: 100vh;
    display: flex;
    width: 100%;
    flex-direction: column;
    background-color: var(--branco);
    & .innerContainerMenuAdministracao{
        display: flex;
        padding: 20px;
        align-items: center;
        flex-direction: column;
        flex: 1;
        color: var(--preto);
        display: flex;
        height: 100%;
        & .FlatListMenuAdministracao{
            display: flex;
            flex-direction: row;
            gap: 10px;
        }
    }
}